.main-header {
  background-color: #FFFFFF;
  color: #ffffff;
  min-height: 65px;
}
.main-header__wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.main-header__logo {

}
.main-header__logo-image {
  display: block;
  padding: 0;
  margin: 0;
  margin-top: 10px;
}
.main-header__logo-text {
  position: absolute;
  left: 50%;
  margin-left: -50px;
}
.main-header__logo-image-text {
  display: block;
  padding: 0;
  margin: 0 auto;
  margin-top: 23px;
}

@media (max-width: $mobile-width-only) {

}

@media (min-width: $tablet-width) {
  .main-header__logo-text {
    margin-left: 12px;
  }

  .main-header__wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media (min-width: $desktop-width) {
  .main-header__wrapper {
    padding-left: 110px;
    padding-right: 110px;
  }
  .main-header__logo-text {
    margin-left: 12px;
  }
}


/* Состояния ссылок/элементов */
@media (min-width: $desktop-width) {
.main-header__logo {
  &:hover {
    opacity: 0.8;
  }

  &:active
   {
    opacity: 0.6;
  }
}
}
